}
.card_img{
width: 100%;
height: 100%;
border-radius: 10px;
background: #ff6060 linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
object-fit: cover;
